// src/components/About.js
import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';

function About() {
  return (
    <Container>
      <Row className="mt-5">
        <Col>
          <h2>Sobre Nosotros</h2>
          <p>
            Enigma Solutions Inc es una empresa lder en el desarrollo de
            software. Nuestro equipo est formado por profesionales
            altamente calificados y apasionados por la tecnologa.
          </p>
          <p>
            Nos especializamos en crear soluciones innovadoras que impulsen
            el xito de nuestros clientes. Con aos de experiencia en la
            industria, hemos entregado proyectos exitosos en diversas
            reas, desde aplicaciones web hasta soluciones empresariales a
            medida.
          </p>
        </Col>
      </Row>
    </Container>
  );
}

export default About;
